@font-face {
  font-family: 'YS Display';
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src:
    url('./fonts/YS_Display-Regular.woff2') format('woff2'),
    url('./fonts/YS_Display-Regular.woff') format('woff'),
    url('./fonts/YS_Display-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'YS Display';
  font-style: normal;
  font-weight: 500;
  font-display: fallback;
  src:
    url('./fonts/YS_Display-Medium.woff2') format('woff2'),
    url('./fonts/YS_Display-Medium.woff') format('woff'),
    url('./fonts/YS_Display-Medium.ttf') format('truetype');
}

