@import './placeholders';
@import './constants';

@mixin font-h1-reg {
  @extend %font-display;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
}

@mixin font-h1-med {
  @extend %font-display;
  font-size: 34px;
  font-style: normal;
  font-weight: 500;
  line-height: 40px;
}

@mixin font-h2-reg {
  @extend %font-display;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

@mixin font-h2-med {
  @extend %font-display;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
}

@mixin font-h3-reg {
  @extend %font-display;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

@mixin font-h3-med {
  @extend %font-display;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

@mixin font-subtitle-reg {
  @extend %font-text;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

@mixin font-subtitle-med {
  @extend %font-text;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

@mixin font-text-reg {
  @extend %font-text;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

@mixin font-text-med {
  @extend %font-text;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}

@mixin font-caption-reg {
  @extend %font-text;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}

@mixin link {
  text-decoration: none;
  transition: 1s;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}

@mixin disabled {
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@mixin list {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin input {
  font-size: 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  margin: 0;

  &::placeholder {
    opacity: 1;
  }

  &:focus {
    outline: 1px solid grey;
  }

  &_invalid {
    color: red;
  }
}

@mixin button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: ease-in-out 350ms;
}

@mixin button-blue {
  @include button;
  background-color: $blue;
  color: $white;
  &:hover {
    background-color: $blue-main;
  }
  &:focus {
    background-color: $blue-main;
  }
  &:disabled {
    background-color: $black-300;
  }
}
