@import '../../styles/abstract/mixins';
@import '../../styles/abstract/constants.scss';

.container {
  min-width: 318px;
  height: 172px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  gap: 20px;
  border-radius: 12px;
  background-color: $grey;
  box-shadow: 0px 4px 6px 0px rgba(176, 190, 197, 0.3);
  transition:
    height 0.8s,
    background-color 0.8s,
    transform 0.3s ease;
  margin-bottom: 5px;
}

.containerLong {
  background: $black-100;
  transition:
    height 0.8s,
    background-color 0.8s;
  height: 360px;
}

.title {
  margin: 0;
  padding-bottom: 12px;
  @include font-h3-med;
  text-overflow: ellipsis; /* Обрывать текст многоточием */
  overflow: hidden;
}

.subtitle_wrap {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.text {
  margin: 0;
  @include font-text-reg;
  white-space: normal;
}

.button_wrap {
  display: flex;
  justify-content: space-between;
}

.hidden_block {
  display: none;
}

.arrow_down {
  background-image: url('../../images/icon/arrow_down.svg');
  background-color: inherit;
  border: 0;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.arrow_up {
  background-image: url('../../images/icon/arrow_up.svg');
}

.arrow_down:hover,
.arrow_up:hover {
  opacity: 0.6;
}
