@import '../../styles/abstract/mixins';

.personalInfo {
  display: flex;
  flex-direction: column;
  padding: 35px 0 0 15px;
}
.personalBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 0 24px;
  width: inherit;
  box-sizing: border-box;
}
.buttons {
  display: flex;
}

.buttons__box {
  display: flex;
  gap: 20px;
  align-items: center;
}

.paper {
  width: 100%;
  box-shadow:
    0px 4px 6px 0px rgba(176, 190, 197, 0.3),
    0px 8px 24px 0px rgba(176, 190, 197, 0.3);
  border-radius: 12px;
}
.infografics {
  width: 278px;
  height: 278px;
}
.stats {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.photoBox {
  display: flex;
  margin-right: 50px;
  gap: 34px;
}
.buttons {
  padding: 20px 45px 20px 20px;
  display: flex;
  justify-content: space-between;
}
.containera {
  display: flex;
  max-width: 300px;
  position: relative;
}
.container__title {
  color: red;
}
.container__slider {
  max-width: 300px;
  display: flex;
  justify-content: center;
  text-align: left;
}

.education_container {
  padding: 40px 24px 48px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.education_title {
  margin: 0;
  @include font-h2-med;
}

.education_subtitle {
  margin: 0;
  padding-bottom: 4px;
  @include font-h3-med;
}

.education_text {
  margin: 0;
  @include font-text-reg;
}

.education_blocks_wrap {
  display: flex;
  gap: 24px;
}

.education_block {
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
